import React from 'react';
import Header from "./components/Header/Header";
import PostHeader from "./components/PostHeader/PostHeader";
import withProvider from "./hoc/withProvider";
import FeaturesLayout from "./components/FeaturesLayout/FeaturesLayout";
import PopularCoursesLayout from "./components/PopularCoursesLayout/PopularCoursesLayout";
import CategoriesLayout from "./components/CategoriesLayout/CategoriesLayout";
import EventLayout from "./components/EventsLayout/EventsLayout";
import Footer from "./components/Footer/Footer";

const App = () => {
    return <div>
        <Header/>
        <PostHeader/>
        <FeaturesLayout/>
        <PopularCoursesLayout/>
        <CategoriesLayout/>
        <EventLayout/>
        <Footer/>

    </div>
};

export default withProvider(App);
